:root {
  /*-------------------
        Site Colors
  --------------------*/
  /*---   Neutral  ---*/
  --full-black        : #000000;
  --full-white        : #FFFFFF;
  --off-white         : #fdfdfd;
  --off-black         : #1c191b;
  --dark-white        : #F3F4F5;
  /*-------------------
      Accent Colors
  --------------------*/
  --ui-kicker           : #eb2f59;
  --ui-nut              : var(--text-orange);
  --ui-nut              : var(--text-orange);
  --ui-kicker-light     : #FF0067;
  --ui-nut-light        : #ff7a00;

  /*--ui-focus            : var(--m-a400, #c30c70);*/
  /* Links TODO FIXME states */
  --link                : var(--text-blue);
  --link                : var(--text-blue);
  --link-hover          : var(--header-blue);
  --link-hover          : var(--header-blue);
  --link-active         : var(--text-green);
  --link-active         : var(--text-green);
  /* special .underlined class ... */
  --link-underline      : none;
  --link-underline-hover: none;

  /*-------------------
    Highlighted Text
  --------------------*/
  --bg-mark             : var(--green);
  --bg-mark             : var(--green);
  --color-mark          : var(--white);
  --color-mark          : var(--white);
  --bg-highlight        : var(--yellow);
  --bg-highlight        : var(--yellow);
  --bg-highlight-input  : rgba(100, 100, 100, 0.4);
  --color-highlight-input: var(--text);
  --color-highlight-input: var(--text);

  /*---   Transparent  ---*/
  --alpha-black       : rgba(0, 0, 0, 0.05); /* transparentBlack */
  --alpha-black-subtle: rgba(0, 0, 0, 0.03);
  --alpha-black-strong: rgba(0, 0, 0, 0.08);
  --alpha-black-intense:rgba(0, 0, 0, 0.13);
  --alpha-white       : rgba(255, 255, 255, 0.08);
  --alpha-white-subtle: rgba(255, 255, 255, 0.05);
  --alpha-white-strong: rgba(255, 255, 255, 0.13);
  --alpha-white-intense:rgba(255, 255, 255, 0.21);


  /*-------------------
       Brand Colors
  --------------------*/
  --color-redaktor    : #FF7A00;
  --color-facebook    : #3B5998;
  --color-twitter     : #0084B4;
  --color-googlePlus  : #DC4A38;
  --color-linkedIn    : #1F88BE;
  --color-youtube     : #CC181E;
  --color-instagram   : #49769C;
  --color-pinterest   : #00ACED;
  --color-vk          : #4D7198;




  /*-------------------
       derived
  --------------------*/
  /* Positive / Negative Dupes */
  --color-success : var(--color-positive);
  --color-success : var(--color-positive);
  --color-border-success : var(--color-border-positive);
  --color-border-success : var(--color-border-positive);
  --bg-success : var(--bg-positive);
  --bg-success : var(--bg-positive);
  --header-success : var(--header-positive);
  --header-success : var(--header-positive);
  --text-success : var(--text-positive);
  --text-success : var(--text-positive);
  --color-error : var(--color-negative);
  --color-error : var(--color-negative);
  --color-border-error : var(--color-border-negative);
  --color-border-error : var(--color-border-negative);
  --bg-error : var(--bg-negative);
