@font-face {
  font-family: 'dojo2BaseTheme';
  src:
    url('./fonts/dojo2BaseTheme.ttf?nocf9g') format('truetype'),
    url('./fonts/dojo2BaseTheme.woff?nocf9g') format('woff'),
    url('./fonts/dojo2BaseTheme.svg?nocf9g#dojo2') format('svg');
  font-weight: normal;
  font-style: normal;
}
:root {}
.root, .root * { -webkit-box-sizing: border-box; box-sizing: border-box; }
.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'dojo2BaseTheme' !important;
  speak: none;
  font-size: inherit;
  font-style: normal;
  font-weight: inherit;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  /* TODO for FINAL iconFont - iconFont is not in _variables yet
   * anything else breaks baseline :
  */
  line-height: 0;
  /* <--- */
  /*
  padding-bottom: inherit;
  margin-top: inherit;
  */
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.compactIcon::before,
.extendIcon::before {
  font-size: 24px;
  line-height: 16px;
}
.compactIcon::before {
  content: "≕";
}
